<template>
  <div>
	<el-row style="margin-bottom:20px;">
	    <el-card shadow="always">
	    	<span class="content-title">用户管理</span>
			<el-button type="success" @click="openAddDialog()">添加</el-button>
			<el-button type="text" style="float: right; padding: 3px 0" @click="moreSelect"><i :class="selectClass"></i>更多选项</el-button>
			<div class="advanced-box" v-if="content_show">
				<el-form :inline="true" :model="formUsers" class="demo-form-inline">
					<el-form-item label="姓名:">
						<el-input v-model="formUsers.user_name"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="onSearch">查询</el-button>
					</el-form-item>
				</el-form>
			</div>
	    </el-card>
	</el-row>
	<el-table
		border
		:data="tableData"
	>
		<el-table-column
			prop="index"
			label="序号"
			align="center"
			width="80">
		</el-table-column>
		<el-table-column
			prop="user_name"
			label="用户姓名"
			align="center"
			width="150">
		</el-table-column>
		<el-table-column
			prop="mobile"
			label="手机号码"
			align="center"
			width="150">
		</el-table-column>
		<el-table-column
			prop="sex"
			label="性别"
			align="center"
			width="80">
		</el-table-column>
		<el-table-column
			prop="status"
			label="状态"
			align="center"
			width="100">
		</el-table-column>
		<el-table-column
			prop="mail"
			label="邮箱"
			align="center">
		</el-table-column>
		<el-table-column
			prop="create_time"
			label="创建时间"
			align="center"
			width="200">
		</el-table-column>
		<el-table-column
			prop="status"
			label="操作"
			align="center"
			width="170">
			<template slot-scope="scope" v-if="scope.row.status!=2">
		        <el-button
		          size="mini"
		          type="primary"
		          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
		        <el-button
		          size="mini"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
		    </template>
		</el-table-column>
	</el-table>
	<div class="block pager">
	  <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page.sync="currentPage"
	      :page-sizes="[15, 20, 30]"
	      :page-size="page_size"
	      layout="sizes, prev, pager, next"
	      :total="total">
	  </el-pagination>
	</div>
	<el-dialog
	  title="添加"
	  :visible.sync="dialogVisible"
	  width="35%"
	  :append-to-body='true'>
	  <div class="el-dialog-user">
	  <el-form label-position="top" :rules="rules" ref="formLabelAlign" label-width="80px" :model="formLabelAlign">
		  <el-form-item label="帐号" prop="user_name">
			<el-input v-model="formLabelAlign.user_name"></el-input>
		  </el-form-item>
		  <el-form-item label="中文名" prop="name_cn">
			<el-input v-model="formLabelAlign.name_cn"></el-input>
		  </el-form-item>
		  <el-form-item label="英文名" prop="name_en">
			<el-input v-model="formLabelAlign.name_en"></el-input>
		  </el-form-item>
		  <el-form-item label="性别">
		    <el-radio-group v-model="formLabelAlign.sex">
		      <el-radio :label="1">男</el-radio>
		      <el-radio :label="2">女</el-radio>
		    </el-radio-group>
		  </el-form-item>
		  <el-form-item label="手机号" prop="mobile">
			<el-input type="number" v-model="formLabelAlign.mobile"></el-input>
		  </el-form-item>
		  <el-form-item label="邮箱" prop="mail">
			<el-input v-model="formLabelAlign.mail"></el-input>
		  </el-form-item>
	  </el-form>
	  </div>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="cancleDialog">取 消</el-button>
	    <el-button type="primary" @click="submitDialog('formLabelAlign')">提 交</el-button>
	  </span>
	</el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
import { utcTimeChange } from '@/utils';
export default {
	data() {
	  var validateMobile = (rule, value, callback) => {
	  	var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
	  	if (value === '') {
          callback(new Error('请输入手机号'));
        } else if(!reg.test(this.formLabelAlign.mobile)){
	  		callback(new Error('手机号格式不正确'));
	  	}else {
          callback();
        }
      };
      var validateEmail = (rule, value, callback) => {
	  	var regEmail=new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
	  	if(value != '' && !regEmail.test(this.formLabelAlign.mail)){
	  		callback(new Error('邮箱格式不正确'));
	  	}else {
          callback();
        }
      };
      return {
        selectClass:"el-icon-arrow-down",
        content_show:false,
        formUsers: {
        	user_name: ''
        },
        //表格
        tableData: [],
		currentPage:1,
		page_size:15,
		total:0,
		//弹出框
		dialogVisible: false,
		//表单
		formLabelAlign: {
          user_name: '',
          passwprd:'',
          sex: 1,
          mail: '',
          name_cn:'',
          name_en:'',
          mobile:''
        },
        rules: {
          user_name: [{ required: true, message: '请输入姓名', trigger: 'blur' }],
          mobile: [{ required: true, message: '请输入手机号', trigger: 'blur' },
          	{ validator: validateMobile, trigger: 'blur' }],
          mail :[{ validator: validateEmail, trigger: 'blur' }]
        }
      };
    },
    mounted() {
    	this.getUserList("1","15");
    },
    methods: {
      handleSizeChange(val) {
        this.getUserList(this.currentPage,val);
      },
      handleCurrentChange(val){
      	this.getUserList(val,this.page_size);
      },
      openAddDialog(){
      	this.formLabelAlign = {
      	  user_name: '',
          passwprd:'',
          sex: 1,
          mail: '',
          name_cn:'',
          name_en:'',
          mobile:''
        };
      	this.dialogVisible = true;
      },
      moreSelect(){
      	if(this.selectClass=="el-icon-arrow-down"){
      		this.selectClass = "el-icon-arrow-up";
      		this.content_show = true;
      	}else{
      		this.selectClass = "el-icon-arrow-down";
      		this.content_show = false;
      	}
      },
      onSearch() {
      	this.getUserList("1","15");
      },
      submitDialog(formType){
      	var that = this;
      	this.$refs[formType].validate(valid => {
	        if (valid) {
	        	that.dialogVisible = false;
	        	var method = '';
	        	var info = '';
	        	if(that.formLabelAlign.hasOwnProperty("user_id"))
	        	{
	        		method = 'core/user/update';
	        		info = '修改成功!';
	        	}else{
	        		method = 'core/user/create';
	        		info = '添加成功!';
	        	}
	        	that.submitUserInfo(method,that.formLabelAlign,info);
	        } else {
	          return false;
	        }
	    });
      },
      cancleDialog(){
      	this.dialogVisible = false;
      },
      handleEdit(index, row) {
      	var that = this;
        that.dialogVisible = true;
        axios.get('core/user/details/' + row.user_id, '').then(function(response){
			var code = response.code;
			if(0 == code){
				var rdata = response.data;
				that.formLabelAlign = rdata;
			}else{
			}
		},function(error){
		});
      },
      handleDelete(index, row) {
      	var that = this;
      	this.$confirm('是否确认删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        	var params = {user_id:row.user_id};
        	that.submitUserInfo('core/user/delete',params,'删除成功!');
        }).catch(() => {
        });
      },
      getUserList(page_index,page_size){
      	var params = {user_name:this.formUsers.user_name,page_index:page_index,page_size:page_size};
      	var that = this;
		axios.get('core/user/pageList', {params}).then(function(response){
			var code = response.code;
			if(0 == code){
				var array = [];
				var rdata = response.data;
				var count = rdata.count;
				var page_index = rdata.page_index;
				var page_size = rdata.page_size;
				that.currentPage = page_index;
				that.page_size = page_size;
				that.total = count;
				var des_num = (page_index - 1) * page_size;
				var json = rdata.rows;
				for(var i = 0;i < json.length;i++){
					var user_id = json[i].user_id;
					var user_name = json[i].user_name;
					var mobile = json[i].mobile;
					var sex = json[i].sex;
					var status = json[i].status;
					var mail = json[i].mail;
					var create_time = utcTimeChange(json[i].create_time);
					var sex_name = "男";
					if(sex == 2){
						sex_name = "女";
					}
					var status_name = "正常";
					if(status == 1){
						status_name = "禁用";
					}else if(status == 2){
						status_name = "删除";
					}
					array.push({index:(des_num + i + 1),user_id:user_id,user_name:user_name,mobile:mobile,sex:sex_name,status:status_name,mail:mail,create_time:create_time}); 
				}
				that.tableData = array;
			}else{
				//console.log(code);
			}
		},function(error){
			//console.log(error);
		});
      },
      submitUserInfo(method,params,info){
      	var that = this;
		axios.post(method, params).then(function(response){
			var code = response.code;
			if(0 == code){
				that.$message({
		            type: 'success',
		            message: info
		        });
				that.getUserList(that.currentPage,that.page_size);
			}else{
				that.$message({
		            type: 'success',
		            message: '操作失败:' + response.message
		        });
			}
		},function(error){
		});
      }
    }
};
</script>

<style scoped>
.content-title{
	margin-right:20px;
}
.advanced-box{
	padding:20px 10px;
	margin-top:15px;
	background-color:#f7f7f7;
}
.pager{
	float:right;
	padding:10px 0;
}
.el-dialog__header{
	border-bottom: 1px solid #CCC;
}
.el-dialog-user{
	height:330px !important;
	padding-right:15px;
	overflow-y:scroll;
}
.el-dialog__footer{
	border-top: 1px solid #CCC;
}
.el-form-item__label{
	padding-bottom:0 !important;
}
</style>